.ArticleView-container {
    width: 70%;
    min-height: 100vh;
    text-align: justify;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.ArticleView-element {
    width: 100%;
}

.NoArticle-container {
    width: 100%;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 2em;
}

.ArticleView-image {
    margin-top: 1em;
    margin-bottom: 1em;
    width: 70%;
}

@media screen and (max-width: 60em) {
    .ArticleView-container {
        width: 90%;
    }

    .ArticleView-image {
        width: 100%;
    }
}



